import React from 'react';
import { Box, Center, Text, Heading, Button } from '@chakra-ui/react';
import { FaInstagram } from 'react-icons/fa';

import WithAnimation from '@components/Common/WithAnimation';
import Image from '@components/Common/LazyImage';

import useInvitation from '@/hooks/useInvitation';
import txtWording from './locales';

import { DEFAULT_LANGUAGE as lang } from '@/constants/features-flag';
import { BG_FILTER } from '@/constants/assets';

/**
 * function to render Filter
 * @returns {JSX.Element}
 */
function FilterSection() {
  const isInvitation = useInvitation();
  
  const handleClickFilterIG = () => {
    window.open('https://www.instagram.com/ar/714442193198614/', '_blank');
  };

  return (
    <Box>
      {isInvitation && (
        <Box>
          <WithAnimation>
            <Heading fontSize="4xl" textAlign="center" color="mainColor" margin="40px 0 12px" fontFamily="fantasy" fontWeight="normal">
              Instagram <br />Wedding Filter
            </Heading>
          </WithAnimation>
          <WithAnimation>
            <Text align="center" fontSize="sm" color="mainColor" marginBottom="16px" padding="0 12px 0 12px">
              <span dangerouslySetInnerHTML={{ __html: txtWording.subTitle[lang] }} />
            </Text>
          </WithAnimation>
          <WithAnimation>
            <Box margin="24px 0 24px">
              <Center>
                <Image src={BG_FILTER} maxW="70%" borderRadius="32px" filter="drop-shadow(2px 4px 6px black)" />
              </Center>
            </Box>
          </WithAnimation>
          <WithAnimation>
            <Center marginBottom="24px">
              <Button
                leftIcon={<FaInstagram />}
                size="sm"
                fontWeight="light"
                bgColor="mainColor"
                textColor="white"
                colorScheme="blackAlpha"
                onClick={handleClickFilterIG}
              >
                {txtWording.textButton[lang]}
              </Button>
            </Center>
          </WithAnimation>
        </Box>
      )}
    </Box>
  );
}

export default React.memo(FilterSection);